import 'rxjs/add/operator/map';
import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {Observable} from 'rxjs/Rx';
import {Response} from "@angular/http";
import {Configuration} from "../_codebit/app.constant";


@Injectable()
export class ContactService {

    private getContactUrl: string;
    private enrolContactUrl: string;
    private deleteContactsUrl: string;
    private editContactUrl: string;

    constructor(private http: HttpClient, private _configuration: Configuration) {
        this.getContactUrl = Configuration.API + 'getEnrollContacts'
        this.enrolContactUrl = Configuration.API + 'enrollContact'
    }

    getContacts() {
        return this.http.get(this.getContactUrl)

    }
    enrollContact(data :any ) {
        return this.http.post(this.enrolContactUrl,data)
    }



}




